// src/app/cart/page.js
'use client'

import React, { useState } from 'react'
import Image from 'next/image'
import Link from 'next/link' // Import Link
import { Trash2, Minus, Plus, ShoppingBag } from 'lucide-react'
import { useCart } from '@/context/CartContext'
import { useAuth } from '@/context/AuthContext'
import productsData from '@/datas/Products.json'
import { Button } from '@/components/ui/button'
import { Card, CardContent } from '@/components/ui/card'
import { Separator } from '@/components/ui/separator'
import Navbar from '@/components/home/Navbar'
import AuthModal from '@/components/auth/AuthModal'
import CheckoutModal from '@/components/checkout/CheckoutModal'

export default function CartPage() {
	const {
		cartItems,
		increaseCartQuantity,
		decreaseCartQuantity,
		removeFromCart,
	} = useCart()
	const { isLoggedIn } = useAuth()

	const [isAuthModalOpen, setAuthModalOpen] = useState(false)
	const [isCheckoutModalOpen, setCheckoutModalOpen] = useState(false)

	const cartProducts = cartItems.map(cartItem => {
		const item = productsData.find(p => p.id === cartItem.id)
		return { ...item, quantity: cartItem.quantity }
	})

	const totalPrice = cartProducts.reduce(
		(total, item) => total + item.newPrice * item.quantity,
		0
	)
	const totalOldPrice = cartProducts.reduce(
		(total, item) => total + (item.oldPrice || item.newPrice) * item.quantity,
		0
	)
	const totalSavings = totalOldPrice - totalPrice

	const formatPrice = price => new Intl.NumberFormat('uz-UZ').format(price)

	const handleCheckoutClick = () => {
		if (isLoggedIn) {
			setCheckoutModalOpen(true)
		} else {
			setAuthModalOpen(true)
		}
	}

	const handleLoginSuccess = () => {
		setTimeout(() => setCheckoutModalOpen(true), 500)
	}

	if (cartItems.length === 0) {
		return (
			<div className='min-h-screen bg-gray-50 pb-20'>
				<Navbar />
				<div className='flex flex-col items-center justify-center h-[60vh] text-center px-4'>
					<div className='bg-gray-200 p-6 rounded-full mb-4'>
						<ShoppingBag className='w-16 h-16 text-gray-400' />
					</div>
					<h1 className='text-2xl font-bold text-gray-800 mb-2'>
						Savatingiz hozircha bosh
					</h1>
					<p className='text-gray-500 mb-6'>
						Mahsulotlarni tanlash uchun bosh sahifaga o'ting
					</p>
					<Link href='/'>
						<Button className='bg-orange-500 hover:bg-orange-600 text-white px-8'>
							Xarid qilish
						</Button>
					</Link>
				</div>
			</div>
		)
	}

	return (
		<div className='min-h-screen bg-gray-50 pb-20'>
			<Navbar />

			<AuthModal
				isOpen={isAuthModalOpen}
				onClose={() => setAuthModalOpen(false)}
				onLoginSuccess={handleLoginSuccess}
			/>

			<CheckoutModal
				isOpen={isCheckoutModalOpen}
				onClose={() => setCheckoutModalOpen(false)}
			/>

			<div className='container mx-auto px-4 py-8'>
				<h1 className='text-2xl font-bold mb-6 flex items-center gap-2'>
					Savat{' '}
					<span className='text-gray-400 text-lg font-normal'>
						{cartItems.length} ta mahsulot
					</span>
				</h1>

				<div className='flex flex-col lg:flex-row gap-6'>
					<div className='flex-1 space-y-4'>
						<Card className='border-none shadow-sm'>
							<CardContent className='p-0'>
								{cartProducts.map((item, index) => (
									<div key={item.id}>
										<div className='p-4 flex gap-4'>
											{/* LINK: Rasmga bosganda */}
											<Link
												href={`/product/${item.id}`}
												className='block relative w-20 h-20 sm:w-28 sm:h-28 bg-gray-50 rounded border flex-shrink-0'
											>
												<Image
													src={item.images[0].imageUrl}
													alt={item.nomi}
													fill
													className='object-contain p-2 mix-blend-multiply'
												/>
											</Link>

											<div className='flex-1 flex flex-col justify-between'>
												<div className='flex justify-between items-start gap-2'>
													{/* LINK: Nomga bosganda */}
													<Link
														href={`/product/${item.id}`}
														className='hover:text-blue-600 hover:underline'
													>
														<h3 className='text-sm sm:text-base font-medium text-gray-800 line-clamp-2'>
															{item.nomi}
														</h3>
													</Link>
													<button
														onClick={() => removeFromCart(item.id)}
														className='text-gray-400 hover:text-red-500 p-1'
													>
														<Trash2 className='w-5 h-5' />
													</button>
												</div>

												<div className='flex justify-between items-end mt-2'>
													<div className='flex items-center border rounded-lg overflow-hidden'>
														<button
															onClick={() => decreaseCartQuantity(item.id)}
															className='p-2 hover:bg-gray-100'
														>
															<Minus className='w-4 h-4 text-gray-500' />
														</button>
														<span className='w-8 text-center text-sm font-semibold'>
															{item.quantity}
														</span>
														<button
															onClick={() => increaseCartQuantity(item.id)}
															className='p-2 hover:bg-gray-100'
														>
															<Plus className='w-4 h-4 text-gray-500' />
														</button>
													</div>
													<div className='text-right'>
														<div className='font-bold text-blue-900 text-lg'>
															{formatPrice(item.newPrice * item.quantity)} so'm
														</div>
														{item.oldPrice && (
															<div className='text-xs text-gray-400 line-through'>
																{formatPrice(item.oldPrice * item.quantity)}{' '}
																so'm
															</div>
														)}
													</div>
												</div>
											</div>
										</div>
										{index !== cartProducts.length - 1 && <Separator />}
									</div>
								))}
							</CardContent>
						</Card>
					</div>

					<div className='lg:w-[350px] space-y-4'>
						<Card className='border-none shadow-sm sticky top-24'>
							<CardContent className='p-6'>
								<h2 className='text-lg font-bold mb-4'>Buyurtmangiz</h2>
								<div className='space-y-3 text-sm mb-4 text-gray-600'>
									<div className='flex justify-between'>
										<span>
											Mahsulotlar (
											{cartItems.reduce((a, b) => a + b.quantity, 0)}):
										</span>
										<span>{formatPrice(totalOldPrice)} so'm</span>
									</div>
									<div className='flex justify-between text-green-600'>
										<span>Tejab qolindi:</span>
										<span>- {formatPrice(totalSavings)} so'm</span>
									</div>
								</div>
								<Separator className='mb-4' />
								<div className='flex justify-between items-center mb-6'>
									<span className='font-bold text-lg'>Jami:</span>
									<span className='font-bold text-xl text-blue-900'>
										{formatPrice(totalPrice)} so'm
									</span>
								</div>

								<Button
									onClick={handleCheckoutClick}
									className='w-full bg-orange-500 hover:bg-orange-600 text-white font-bold py-6 text-base'
								>
									Rasmiylashtirishga o'tish
								</Button>
								<p className='text-xs text-gray-400 mt-4 text-center'>
									Shartlarni qabul qilasiz.
								</p>
							</CardContent>
						</Card>
					</div>
				</div>
			</div>
		</div>
	)
}
