// src/components/home/Navbar.js
'use client'

import React, { useState } from 'react'
import Image from 'next/image'
import Link from 'next/link'
import {
	Search,
	ShoppingCart,
	Menu,
	User,
	Package,
	LogOut,
	X,
} from 'lucide-react'

// Contextlarni ulaymiz
import { useAuth } from '@/context/AuthContext'
import { useCart } from '@/context/CartContext'

// Komponentlar
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import {
	DropdownMenu,
	DropdownMenuContent,
	DropdownMenuItem,
	DropdownMenuLabel,
	DropdownMenuSeparator,
	DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu'
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar'
import { Sheet, SheetContent, SheetTrigger } from '@/components/ui/sheet' // Mobile menu uchun qulayroq

// Biz yaratgan Modal
import AuthModal from '@/components/auth/AuthModal'

const categories = [
	'Oshxona jihozlari',
	'Tozalash texnikasi',
	'Isitish va sovutish',
	'Shaxsiy parvarish',
	'Aqlli uy',
	'Aksiyalar',
]

const Navbar = () => {
	// 1. Global Contextlardan ma'lumot olamiz
	const { isLoggedIn, user, logout } = useAuth()
	const { totalCartCount } = useCart()

	// 2. Lokal statelar
	const [isAuthModalOpen, setAuthModalOpen] = useState(false)
	const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false)

	return (
		<>
			{/* --- AUTH MODAL (Navbar ichida turadi, istalgan joydan chaqirish uchun) --- */}
			<AuthModal
				isOpen={isAuthModalOpen}
				onClose={() => setAuthModalOpen(false)}
				onLoginSuccess={() => setAuthModalOpen(false)}
			/>

			<header className='sticky top-0 z-50 w-full border-b bg-white shadow-sm'>
				<div className='container mx-auto px-4 h-20 flex items-center justify-between gap-4'>
					{/* --- 1. LOGO VA KATALOG --- */}
					<div className='flex items-center gap-6'>
						<Link href='/' className='flex-shrink-0'>
							<Image
								src='/logo.png'
								alt='Tech House Logo'
								width={150}
								height={50}
								className='object-contain h-12 w-auto'
								priority
							/>
						</Link>

						<div className='hidden md:block'>
							<DropdownMenu>
								<DropdownMenuTrigger asChild>
									<Button
										variant='outline'
										className='bg-blue-900 text-white hover:bg-blue-800 hover:text-white border-none flex items-center gap-2'
									>
										<Menu className='h-4 w-4' />
										Katalog
									</Button>
								</DropdownMenuTrigger>
								<DropdownMenuContent className='w-56 mt-2'>
									<DropdownMenuLabel>Bo'limlar</DropdownMenuLabel>
									<DropdownMenuSeparator />
									{categories.map((cat, index) => (
										<DropdownMenuItem key={index} className='cursor-pointer'>
											{cat}
										</DropdownMenuItem>
									))}
								</DropdownMenuContent>
							</DropdownMenu>
						</div>
					</div>

					{/* --- 2. QIDIRUV (SEARCH) --- */}
					<div className='hidden md:flex flex-1 max-w-lg items-center relative'>
						<Input
							type='text'
							placeholder='Mahsulotlarni qidirish...'
							className='pr-10 border-blue-200 focus-visible:ring-blue-900 rounded-r-none'
						/>
						<Button className='rounded-l-none bg-blue-100 text-blue-900 hover:bg-blue-200 border border-l-0 border-blue-200'>
							<Search className='h-5 w-5' />
						</Button>
					</div>

					{/* --- 3. O'NG TOMON (ACTIONS) --- */}
					<div className='hidden md:flex items-center gap-6'>
						{/* Buyurtmalar (Faqat kirganlarga yoki barchaga ko'rsatish mumkin) */}
						<Link
							href='/orders'
							className='flex flex-col items-center text-gray-600 hover:text-blue-900 transition text-xs font-medium'
						>
							<Package className='h-6 w-6 mb-1' />
							Buyurtmalar
						</Link>

						{/* Savat (Real count bilan) */}
						<Link
							href='/cart'
							className='flex flex-col items-center text-gray-600 hover:text-blue-900 transition text-xs font-medium relative'
						>
							<div className='relative'>
								<ShoppingCart className='h-6 w-6 mb-1' />
								{totalCartCount > 0 && (
									<span className='absolute -top-1 -right-2 bg-orange-500 text-white text-[10px] font-bold px-1.5 py-0.5 rounded-full'>
										{totalCartCount}
									</span>
								)}
							</div>
							Savat
						</Link>

						{/* Profil / Kirish */}
						{isLoggedIn ? (
							<DropdownMenu>
								<DropdownMenuTrigger asChild>
									<div className='flex flex-col items-center cursor-pointer'>
										<Avatar className='h-8 w-8 border-2 border-blue-900'>
											<AvatarImage src='https://github.com/shadcn.png' />
											<AvatarFallback>TH</AvatarFallback>
										</Avatar>
										<span className='text-xs font-medium text-gray-700 mt-1 max-w-[60px] truncate'>
											{user?.phone || 'Profil'}
										</span>
									</div>
								</DropdownMenuTrigger>
								<DropdownMenuContent align='end' className='w-48'>
									<DropdownMenuLabel>Mening Hisobim</DropdownMenuLabel>
									<div className='px-2 text-xs text-gray-500 mb-2'>
										{user?.phone}
									</div>
									<DropdownMenuSeparator />
									<DropdownMenuItem asChild>
										<Link href='/orders' className='cursor-pointer w-full'>
											Buyurtmalarim
										</Link>
									</DropdownMenuItem>
									<DropdownMenuSeparator />
									<DropdownMenuItem
										onClick={logout}
										className='text-red-500 cursor-pointer focus:text-red-600'
									>
										<LogOut className='mr-2 h-4 w-4' /> Chiqish
									</DropdownMenuItem>
								</DropdownMenuContent>
							</DropdownMenu>
						) : (
							<Button
								onClick={() => setAuthModalOpen(true)}
								className='bg-orange-500 hover:bg-orange-600 text-white font-semibold shadow-md'
							>
								<User className='mr-2 h-4 w-4' />
								Kirish
							</Button>
						)}
					</div>

					{/* --- 4. MOBILE MENU --- */}
					<div className='md:hidden flex items-center gap-4'>
						<Link href='/cart' className='relative text-gray-700'>
							<ShoppingCart className='h-6 w-6' />
							{totalCartCount > 0 && (
								<span className='absolute -top-2 -right-2 bg-orange-500 text-white text-[10px] font-bold px-1.5 py-0.5 rounded-full'>
									{totalCartCount}
								</span>
							)}
						</Link>

						{/* Mobile Menu Button */}
						<Button
							variant='ghost'
							size='icon'
							onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
						>
							{isMobileMenuOpen ? <X /> : <Menu />}
						</Button>
					</div>
				</div>

				{/* --- MOBILE MENU CONTENT (Expandable) --- */}
				{isMobileMenuOpen && (
					<div className='md:hidden border-t bg-white p-4 absolute w-full left-0 shadow-lg z-40 animate-in slide-in-from-top-2'>
						<div className='flex flex-col gap-4'>
							{/* Mobile Search */}
							<div className='relative w-full'>
								<Input
									type='text'
									placeholder='Qidirish...'
									className='w-full bg-gray-50'
								/>
								<Search className='absolute right-3 top-2.5 h-5 w-5 text-gray-400' />
							</div>

							{/* Mobile Auth */}
							<div className='flex items-center gap-3 border-b pb-4'>
								{isLoggedIn ? (
									<div className='flex items-center gap-3 w-full'>
										<Avatar className='h-10 w-10'>
											<AvatarImage src='https://github.com/shadcn.png' />
											<AvatarFallback>TH</AvatarFallback>
										</Avatar>
										<div className='flex-1'>
											<p className='font-medium text-sm'>Foydalanuvchi</p>
											<p className='text-xs text-gray-500'>{user?.phone}</p>
										</div>
										<Button
											variant='ghost'
											size='icon'
											onClick={() => {
												logout()
												setIsMobileMenuOpen(false)
											}}
										>
											<LogOut className='h-5 w-5 text-red-500' />
										</Button>
									</div>
								) : (
									<Button
										onClick={() => {
											setAuthModalOpen(true)
											setIsMobileMenuOpen(false)
										}}
										className='bg-orange-500 w-full text-white'
									>
										Kirish / Ro'yxatdan o'tish
									</Button>
								)}
							</div>

							<div className='flex flex-col gap-2'>
								<Link
									href='/orders'
									className='flex items-center gap-2 p-2 hover:bg-gray-100 rounded-lg'
								>
									<Package className='h-5 w-5 text-gray-500' />
									<span>Buyurtmalarim</span>
								</Link>
							</div>

							{/* Mobile Categories */}
							<div>
								<p className='font-semibold mb-2 text-sm text-gray-500 uppercase'>
									Kategoriyalar
								</p>
								<div className='grid grid-cols-2 gap-2'>
									{categories.map((cat, idx) => (
										<Button
											key={idx}
											variant='outline'
											className='justify-start text-xs h-9'
										>
											{cat}
										</Button>
									))}
								</div>
							</div>
						</div>
					</div>
				)}
			</header>
		</>
	)
}

export default Navbar
